html,
body {
  height: 100%;
}
body {
  margin: 0 auto;
  box-sizing: border-box;
}
img {
  width: 100%;
  height: auto;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
picture {
  margin: 0;
  padding: 0;
  border: 0;
}
svg {
  fill: currentColor;
  width: 100%;
}
.none {
  display: none;
}
.visualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  width: 320px;
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: "OpenSans", "Arial Narrow", sans-serif;
  font-size: 15px;
  line-height: 30px;
  font-weight: normal;
  color: #444444;
}
