.text-normal {
  font-family: "OpenSans", "Arial Narrow", sans-serif;
  font-size: 15px;
  line-height: 30px;
  font-weight: normal;
  color: @color-dark;
 
}

.text-bold {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  color: @color-black;

  &--green {
    color: @color-green;
  text-transform: uppercase;

  @media(min-width: @width-tablet) {
    line-height: 17px;
    }  
  }
}

.text-green {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  color: @color-green;
  text-transform: uppercase;

  @media(min-width: @width-tablet) {
    line-height: 17px;
  }  
}

